<template>
    <div>
    <div class="menu">
        <ul>
            <tree :detail="mytree"></tree>
        </ul>   
    </div>

    <div class="contents">

        {{$store.state.permission != 'new'}}
        <div v-if="$store.state.user && $store.state.permission != 'new'">
                {{$store.state.selectData}} <br>
                user:{{$store.state.user}}<br>
                permission:{{$store.state.permission}}
                <permission1  v-if="$store.state.permission == 1"></permission1>
                <permission2  v-if="$store.state.permission == 2"></permission2>
                <permission3  v-if="$store.state.permission > 3"></permission3>
                <anime></anime>
        </div>
        <div v-if="$store.state.user && $store.state.permission == 'new'">
                <iphone  v-if="$store.state.permission == 'new'"></iphone>
        </div>
    </div>



    </div>
</template>

<script>
import tree from './tree.vue';
import permission3 from "./permission3";
import Anime from "./Anime";
import Iphone from "./Iphone";
export default {
    name: 'root',
    components: {
        tree:tree,
        permission3:permission3,
        anime:Anime,
        iphone:Iphone

    },
    data() {
        return {
            mytree: {
                name: "root",
                permission:'1',
                node: [
                    {
                    name: "fun1",
                    permission:'1',
                    node: [
                        {
                        name: "fun1-1",
                        permission:'1',
                        },
                        {
                        name: "func1-2",
                        permission:'1',
                        },
                    ],
                    },
                    {
                    name: "func2",
                    permission:'2',
                    node: [
                        {
                        name: "func2-1",
                        permission:'3',
                        },
                    ],
                    },
                ],
            },
        }
    }
}
</script>
<style scoped>
 .menu {
    width:230px;
    float:left;
 }
 .contents {
 width:1500px;
float:left;

 }

</style>


