<template>
    <li @click.stop="toggle(detail.name)"  v-if="$store.state.permission >= detail.permission" >
        
    <span class='list-item'>
      <i class='icon-reorder'>{{detail.name}}</i>
    </span>
        <ul v-show="open" v-if="detail.node && detail.node.length > 0">
            <tree v-for="(item, index) in detail.node"
            :key="index"
            :detail= "item">
            </tree>
        </ul>
    </li>
</template>

<script>
import tree from './tree.vue'
export default {
    name: 'tree',
    props:['detail'],
    components: {tree},
    data () {
        return {
            open:false,
        }                                               
    },
    computed: {
        doneTodosCount() {
            return this.$store.getters.getChartData
        },
    },
  methods:{
    toggle(name){
      if(this.detail.node && this.detail.node.length){
        this.open = !this.open ;
      }else{
        console.log(name);
        this.$store.state.selectData = name;
      }
    }
  }
}
</script>
<style scoped>

.list-item {
  background: #000;
  color: #575757;
  text-align: center;
  height: 2.5em;
  width: 4em;
  vertical-align: middle;
  line-height: 2.5em;
  border-bottom: 1px solid #060606;
  position: relative;
  display: block;
  text-decoration: none;
  box-shadow: -2em 1.5em 0 #e1e1e1;
  -webkit-transition: all .25s linear;
  transition: all .25s linear;
}
.list-item:hover {
  background: #ff6e42;
  color: #fffcfb;
  -webkit-transform: translate(0.9em, -0.9em);
          transform: translate(0.9em, -0.9em);
  -webkit-transition: all .25s linear;
  transition: all .25s linear;
  box-shadow: -2em 2em 0 #e1e1e1;
}
.list-item:hover:before, .list-item:hover:after {
  -webkit-transition: all .25s linear;
  transition: all .25s linear;
}
.list-item:hover:before {
  background: #b65234;
  width: 1em;
  top: .5em;
  left: -1em;
}
.list-item:hover:after {
  background: #b65234;
  width: 1em;
  bottom: -2.5em;
  left: 1em;
  height: 4em;
}
.list-item:before, .list-item:after {
  content: '';
  position: absolute;
  -webkit-transition: all .25s linear;
  transition: all .25s linear;
  width: .5em;
}
.list-item:after {
  height: 4em;
  background: #181818;
  bottom: -2.25em;
  left: 1.5em;
  -webkit-transform: rotate(90deg) skew(0, 45deg);
          transform: rotate(90deg) skew(0, 45deg);
}
.list-item:before {
  height: 2.5em;
  background: #121212;
  top: .25em;
  left: -.5em;
  -webkit-transform: skewY(-45deg);
          transform: skewY(-45deg);
}

</style>


