import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import permission4 from'@/components/permission4'
import login from'@/components/Login'
import root from '@/components/root'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: login
    },
    {
      path: '/root',
      name: 'root',
      component: root
    },
    {
      path: '/per4',
      name: 'permission4',
      component: permission4
    },
    {
      path: '/hello',
      name: 'hello',
      component: HelloWorld
    }
  ]
})



